@import '../common/index.scss';

.App {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.App-header {
    font-size: Font-Size(12px);
    min-height: 48px;
    padding: 0 24px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.8);

    .left {
        margin-right: 10px;

        .img {
            width: 44px;
            height: 44px;
            border-radius: 50%;
        }

        span {
            background: linear-gradient(90deg, #6c5a34, #efd574);
            -webkit-text-fill-color: white;
            -webkit-background-clip: text;
            -webkit-text-stroke: 4px transparent;
            font-size: Font-Size(18px);
            margin-left: 8px;
        }
    }

    .right {
        flex: 1;

        .route-item {
            margin: 0 5px;
            display: flex;
            align-items: center;
            height: 100%;
            padding: 0 10px;
            box-sizing: border-box;
            text-align: center;

            &:hover {
                background-color: #e8e8e8;
            }
        }

        .link-item {
            text-decoration: none;
            color: #333;
        }

        .activeTab {
            border-top: 1px solid red;
        }
    }
}

.App-body {
    flex: 1;
    background-color: #F5F6F7;
    overflow-y: auto;
    padding: 10px;
}

.gua_component {
    text-align: center;
    user-select: none;
    .gua_box {
        width: 80%;
        // max-width: 400px;
        height: 20%;
        background-color: pink;
    }
}

@media screen and (max-device-width: 400px) {
    .App-header {
        flex-direction: column;

        .left {
            margin-right: 0;
            margin-bottom: 5px;
        }

        .right .route-item {
            font-size: Font-Size(13px);
            padding: 0 0;
        }
    }
}